const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    dialogs: [
        { id: 1, name: 'Sveta' },
        { id: 2, name: 'Dima' },
        { id: 3, name: 'Tanya' },
        { id: 4, name: 'Sergei' },
        { id: 5, name: 'Sasha' },
        { id: 6, name: 'Valera' }
    ],
    messages: [
        { id: 1, message: 'Hi' },
        { id: 2, message: 'Ho is your it-kamasytra' },
        { id: 3, message: 'Yo' },
        { id: 4, message: 'Yo' },
        { id: 5, message: 'Yo' }
    ]       
};

const dialogsReducer = (state = initialState, action) => {
    let stateCopy;
    switch (action.type) {
     
            
        case SEND_MESSAGE:
             let body = action.newMessageBody;
            stateCopy = {
                ...state,
        messages: [...state.messages, { id: 6, message: body}]
    };
            return stateCopy;
        default:
            return state;
        }
}


export const sendMessageCreator = (newMessageBody) => ({ type: SEND_MESSAGE, newMessageBody})


export default dialogsReducer;